body {
  background-color: #000;
  font-family: "Roboto", Sans-serif;
}

h2 {
  color: #f2951c;
  font-family: "Roboto", Sans-serif;
  font-weight: 600;
}

.heading {
  clear: both;
  text-align: center;
}

.surface-container {
  max-width: 1000px;
  margin: 50px auto 0;
  border: #f2951c solid 0px;
  background-color: #000;
}

.container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  height: auto;
  margin: 50px 50px;
}

.tablecontainer {
  height: auto;
  margin: 50px 60px;
}

.item {
  flex-basis: 0;
  widt: 150px;
  margin-right: 25px;
  margin-bottom: 50px;
  position: relative;
  -webkit-transition: opacity 0.3s ease;
  -o-transition: opacity 0.3s ease;
  transition: opacity 0.3s ease;
}

.item__img__img {
  border: 0;
}
.item__img {
  border: solid 1px #f2951c;
  background: transparent url(https://placehold.co/150x150) no-repeat;
}

.item__rank {
  background-color: #f2951c;
  position: absolute;
  border-radius: 50%;
  font-size: 12px;
  width: 25px;
  height: 25px;
  line-height: 25px;
  text-align: center;
  top: -10px;
  right: -10px;
  font-weight: bold;
  -webkit-box-shadow: 0 0 3px 1px rgba(0, 0, 0, 0.4);
  box-shadow: 0 0 3px 1px rgba(0, 0, 0, 0.4);
  color: #000;
}

.item__name {
  display: block;
  font-size: 14px;
  font-weight: bold;
  padding: 0 8px;
  margin: 4px 0 0;
  text-decoration: none;
  color: #f2951c;
}
.item__status {
  display: block;
  font-size: 12px;
  color: #f2951c;
  padding: 5px 8px;
  margin: 0px 2px 0;
  text-decoration: none;
}

.item__link:hover {
  text-decoration: none;
}

.ias-foot {
  text-align: center;
  margin: 40px 0;
  font: 12px sans-serif;
  color: #aaa;
}

.ias-foot a {
  color: #999;
}

div.greyGridTable {
  float: left;
  margin-right: 25px;
  margin-bottom: 50px;
  background-color: #000000;
  text-align: left;
  border-collapse: collapse;
  width: 150px;
}
.tableRight {
  margin-left: auto;
  margin-right: 0;
}
.divTable.greyGridTable .divTableCell,
.divTable.greyGridTable .divTableHead {
  padding: 3px 4px;
}
.divTable.greyGridTable .divTableBody .divTableCell {
  font-size: 12px;
  color: #f2951c;
}
.divTable.greyGridTable .divTableHeading {
  background: #000000;
  border-bottom: 1px solid #f2951c;
}
.divTable.greyGridTable .divTableHeading .divTableHead {
  font-size: 14px;
  font-weight: bold;
  color: #f2951c;
  text-align: left;
}
.greyGridTable .tableFootStyle {
  font-size: 12px;
}
/* DivTable.com */
.divTable {
  display: table;
}
.divTableRow {
  display: table-row;
}
.divTableHeading {
  display: table-header-group;
}
.divTableCellRight {
  font-size: 12px;
  color: #f2951c;
  display: table-cell;
  text-align: right;
}

.divTableCellLeft {
  display: table-cell;
}

.divTableHead {
  display: table-cell;
}

.divTableHeading {
  display: table-header-group;
}
.divTableFoot {
  display: table-footer-group;
}
.divTableBody {
  display: table-row-group;
}
